.header {
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: rgb(208, 208, 208);
  justify-content: center;
  padding: 0px auto 15px;
  margin-bottom: 2.5em;
}

.headerElements {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  border: 1px solid #99560f;
  margin: 0.3em;
}

.headerLogo img {
  width: 350px;
  height: auto;
  margin-top: .25em;
  margin-bottom: -4px;
  margin-right: .5em;
}

